@import './styles/variables.scss';

.App {
  position: relative;
  &::after {
    content: '';
    z-index: 1;
    position: absolute;
    right: 0;
    top:0;
    height: 100%;
    width: 20%;
    background: linear-gradient(90deg, rgba(16, 16, 47, 0) 0%, rgba(2, 2, 23, 0.4) 100%);
  }
  &::before {
    content: '';
    z-index: 1;
    position: absolute;
    left: 0;
    top:0;
    height: 100%;
    width: 20%;
    background: linear-gradient(90deg, rgba(2, 2, 23, 0.4) 0%, rgba(16, 16, 47, 0) 100%);
  }
  & header, footer, main {
    position: relative;
    z-index: 2;
  }
}

.testPlate {
  width: 282px;
  height: 240px;
  display: flex;
  justify-content: center;
  align-items: center;
}